import React, { useState } from 'react';
import { Switch, Route } from 'react-router-dom';
import "./Content.css";

import Home from './Home';

function Content(props) {
    return <>
        <div {...props} className="content">
            <Switch>
                <Route exact path="/"><Home /></Route>
            </Switch>
        </div>
    </>
}

export default Content;