import React, { useState } from 'react';
import { Link } from "react-router-dom";
import { Navbar, Nav, NavDropdown, Form, FormControl, Button, Container, Row, Col } from 'react-bootstrap';
import "./Header.css";
// import villageMap1 from "../images/villageMap1.png";
// import villageMap2 from "../images/villageMap2.png";
// import villageCenter1 from "../images/villageCenter1.png";
// import villageCenter2 from "../images/villageCenter2.png";
// import img_header from "../images/header.png";
// import img_feilds1 from "../images/feilds1.png";
// import img_feilds2 from "../images/feilds2.png";

function Header(props) {
    let dateObject = new Date();
    let date = () => { dateObject.setTime(Date.now()); return dateObject.toLocaleTimeString("ar-SA", { hour12: true, hour: "numeric", minute: "2-digit" }); }
    let [time, setTime] = useState(date());
    setInterval(() => { setTime(date()); }, 3000);
    return <div className="header">
        <div className="top_header"><div className="game_header" alt="header"></div></div>
        <Navbar variant="light" className="t-navbar">
            <Navbar.Collapse id="basic-navbar-nav">
                <Nav className="mx-auto">
                    {/* <span className="msTime">زمن الاستجابة: {props.responseTime}ms</span> */}
                    <div className="header-items">
                        <Link className="nav-link" to="/"><Button variant={"outline-dark"}>الرئيسية</Button></Link>
                        <Link className="nav-link" to="/login"><Button on variant={"outline-dark"}>الدخول</Button></Link>
                        <Link className="nav-link" to="/register"><Button variant={"outline-dark"}>التسجيل</Button></Link>
                        <span className="server-time">{time}</span>
                    </div>
                    {/* <Nav.Link href="#home">Home</Nav.Link>
                    <NavDropdown title="Dropdown" id="basic-nav-dropdown">
                        <NavDropdown.Item href="#action/3.1">Action</NavDropdown.Item>
                        <NavDropdown.Item href="#action/3.2">Another action</NavDropdown.Item>
                        <NavDropdown.Item href="#action/3.3">Something</NavDropdown.Item>
                        <NavDropdown.Divider />
                        <NavDropdown.Item href="#action/3.4">Separated link</NavDropdown.Item>
                    </NavDropdown> */}
                </Nav>
            </Navbar.Collapse>
        </Navbar>
    </div>
}

export default Header;